.ResumeViewer {
  font-size: 12px;
  flex-direction: column;
}

.ResumeViewer .link-wrapper {
  font-size: 12px;
  position: relative;
  margin: 5px;
  line-height: 13px;
  font-style: normal;
  font-weight: normal;
  padding: 6px 12px;
  background: #fdfdfd;
  border: 1px solid #e2e2e2;
  box-sizing: border-box;
  display: inline-block;
}

.ResumeViewer a {
  text-decoration: none;
  color: #525252;
  padding: 0 2px;
}

.ResumeViewer .document{
    box-shadow: 5px 5px 5px -5px #525252;
}