.ProfessionalClass {
  width: 470px;
  background-color: #fdfdfd;
  border: 1px solid #e2e2e2;
  box-sizing: border-box;
  margin: 25px;
  position: relative;
}

.ProfessionalClass .wrapper {
  margin: 20px 20px 25px;
}

.ProfessionalClass .sub-heading {
  font-size: 12px;
  padding: 13px 0 0px;
  color: #525252;
}

.ProfessionalClass .title {
  color: #898989;
  font-size: 12px;
}

.ProfessionalClass .instructor {
  font-size: 8px;
  font-style: italic;
  letter-spacing: 0.05em ;
  color: #898989;
}

.ProfessionalClass .date {
  font-size: 10px;
  text-align: right;
  font-style: italic;
  font-weight: normal;
  line-height: 10px;
  text-transform: uppercase;
  color: rgb(var(--color));
}

.ProfessionalClass .description {
  /* text-transform: lowercase; */
  font-size: 11px;
  color: #000;
}

.ProfessionalClass .courses{
    padding: 25px 0 ;
}

.ProfessionalClass .border-bottom{
    border-bottom: 1px solid #898989;
}