.ImageCarousel {
  position: absolute;
  width: 100%;
  left: -25px;
  margin: 15px 0;
  background-color: #eeeeee;
  border-radius: 4px;
  box-shadow: 5px 5px 5px -5px #525252;
  color: #525252;
  font-size: 10px;
}

.ImageCarousel .images {
  width: 250px;
  filter: grayscale(0.4);
}

.ImageCarousel .heading{
  padding: 10px 12px 5px;
}

.ImageCarousel .dot {
  width: 4px;
  height: 4px;
  margin: 1px;
  background-color: #e2e2e2;
  border-radius: 2px;
}

.ImageCarousel .icons > *{
  padding-right: 4px;
}

.ImageCarousel .selected {
  background-color: #aaaaaa;
}

.ImageCarousel .comments {
  color: #aaaaaa;
  font-size: 9px;
}

.ImageCarousel .caption {
  font-size: 9px;
}

.ImageCarousel .ellipses{
  position: absolute;
  right: 10px;
  font-weight: bold;
  padding-bottom: 2px;
}

@media (max-width: 700px) {
  .ImageCarousel {
    left: 5px;
    width: 90%;
  }

  .ImageCarousel .images {
    width: 220px;
  }
}
