.ContentContainer {
  border: 1px solid #e2e2e2;
  width: 65%;
  height: 100%;
  position: relative;
  min-width: 535px;
  background-color: #f8f8f8;
  box-shadow: 5px 5px 5px -5px #525252;
}

.ContentContainer .shadow {
  box-shadow: 0 8px 5px -6px #525252;
    width: 15px;
    right: -15px;
    position: absolute;
    bottom: 0;
    height: 100%;
    z-index: 0;
}

.ContentContainer .list{
  margin-bottom: 75px;
}