:root {
  cursor: url("https://twpeng-portfolio.s3-us-west-1.amazonaws.com/cursors/minimal-01.cur"), auto !important;
  --red: 222;
  --green: 198 ;
  --blue: 198 ;
  --color: var(--red),var(--green),var(--blue);
  --backgroundColor: 236, 221, 221;
  --patternColor1: 232, 178, 208;
  --patternColor2: 142, 188, 238; 

}

/* yellow 232, 218, 148 */

a,
a:hover,
a > *,
button,
button *,
:focus,
input[type=color] {
  cursor: url("https://twpeng-portfolio.s3-us-west-1.amazonaws.com/cursors/minimal-15.cur"), auto !important;
  transform: translate(0);
}

a > div:hover {
  border: 1px solid #525252 !important;
}

a > mark:hover {
  transform: translateY(1px);
}

html,
body,
#root,
.App {
  height: 100vh;
  width: 100vw;
  min-height: 100%;
  min-width: 100%;
  background-color: rgb(var(--backgroundColor));
}


body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #f5f5f5;
  letter-spacing: 0.15em;
}

code {
  font-family: Menlo, Monaco, Consolas, "Courier New", monospace;
}

@font-face {
  font-family: "The Secret";
  src: local("The Secret"), local("TheSecret"),
    url("./fonts/TheSecret-Regular.woff") format("woff"),
    url("./fonts/TheSecret-Regular.otf") format("opentype");
  font-weight: 300;
  font-style: normal;
}

.font-primary {
  font-family: "Montserrat", sans-serif !important;
}

.font-secondary {
  font-family: "Libre Franklin", sans-serif !important;
  text-transform: uppercase;
}

.font-accent {
  font-family: "Abril Fatface", serif !important;
}

.font-cursive {
  font-family: "The Secret", cursive !important;
}

.font-mono {
  font-family: "Roboto Mono", monospace !important;
}

/* font-family: 'Abril Fatface', cursive;
font-family: 'Libre Franklin', sans-serif;
font-family: 'Montserrat', sans-serif; */

.overflow-hidden {
  overflow: hidden;
}

mark {
  display: inline-block;
  line-height: 0em;
  padding-bottom: 0.5em;
  background-color: rgba(var(--color), 0.6);
  color: #525252;
}

.row {
  margin: 0 !important;
}

[data-title]:hover:after {
  opacity: 1;
  transition: all 0.1s ease 0.3s;
  visibility: visible;
}
[data-title]:after {
  text-decoration: none;
  font-family: "Libre Franklin", sans-serif !important;
  text-transform: lowercase;
  content: attr(data-title);
  background-color: #fdfdfd;
  color: #aaa;
  position: absolute;
  padding: 1px 5px 2px 5px;
  bottom: -1.6em;
  left: 100%;
  white-space: nowrap;
  opacity: 0;
  border: 1px solid #e2e2e2;
  z-index: 99999;
  visibility: hidden;
  font-size: 9px;
  line-height: 11px;
  letter-spacing: 0.15em;
}
[data-title] {
  position: relative;
}


::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}

/* Track */
::-webkit-scrollbar-track {
  background: rgba(255,255,255,.3);
}
 
/* Handle */
::-webkit-scrollbar-thumb {
  background: rgba(var(--color), 0.3);
  border: 1px solid rgba(255,255,255,.5);
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: rgba(var(--color), 0.4); 
}