.SearchTags {
  margin: 0 0 15px;
  background-color: #eeeeee;
  width: 115%;
  border-radius: 4px;
  box-shadow: 5px 5px 5px -5px #525252;
  color: #aaaaaa;
}

.SearchTags .heading{
    display: inline-block;
}

.SearchTags .mic{
    padding-top: 3px;
}

.SearchTags .top{
    padding-top: 10px;
    margin: 0 20px 10px !important;
    border-bottom: 1px solid #e2e2e2;
}

.SearchTags .bottom{
    padding: 0 20px 10px;
    font-size: 11px;
}
.SearchTags .bottom > *{
    padding-bottom: 5px;
}