.Navbar {
  font-style: normal;
  font-weight: normal;
  font-size: 11px;
  line-height: 13px;
  letter-spacing: 0.15em;
  color: #525252;
  background: #fdfdfd;
  border: 1px solid #e2e2e2;
  box-sizing: border-box;
  display: inline-block;
  padding: 6px 12px;
}

.Navbar a {
  text-decoration: none;
  color: #525252;
  padding: 0 2px;
}
