.Work {
  width: 470px;
  background-color: #fdfdfd;
  border: 1px solid #e2e2e2;
  box-sizing: border-box;
  margin: 25px;
  position: relative;
}

.Work .wrapper {
  margin: 40px 20px 25px;
}

.Work .image-wrapper {
  border: 1px solid #e5e5e5;
  box-sizing: border-box;
}

.Work .company-info {
  font-size: 12px;
  padding: 13px 0 10px;
}

.Work .description {
  padding: 20px 0;
  font-size: 12px;
  line-height: 14px;
  color: #898989;
}

.Work .date {
  font-size: 10px;
  float: right;
  font-style: italic;
  font-weight: normal;
  line-height: 10px;
  text-transform: uppercase;
  color: rgb(var(--color));
}
.Work .line {
  font-style: normal;
  font-weight: normal;
  line-height: 10px;
  text-transform: uppercase;
  color: #c4c4c4;
}

.Work .location {
  font-style: normal;
  font-weight: normal;
  line-height: 10px;
  text-transform: lowercase;
  color: #525252;
}

.Work .keywords {
  font-style: italic;
  font-size: 10px;
  line-height: 12px;
  text-align: center;
  text-transform: lowercase;
  color: #bbbbbb;
}
