.Projects {
  width: 470px;
  background-color: #fdfdfd;
  border: 1px solid #e2e2e2;
  box-sizing: border-box;
  margin: 25px;
  position: relative;
}

.Projects .wrapper {
  margin: 40px 20px 25px;
}

.Projects .image-wrapper {
  border: 1px solid #e5e5e5;
  box-sizing: border-box;
}

.Projects .name-info {
  font-size: 12px;
  padding: 13px 0 10px;
}

.Projects .description {
  padding: 20px 0;
  font-size: 12px;
  line-height: 14px;
  color: #898989;
}

.Projects .line {
  font-style: normal;
  font-weight: normal;
  line-height: 10px;
  text-transform: uppercase;
  color: #c4c4c4;
}

.Projects .keywords {
  font-style: italic;
  font-size: 10px;
  line-height: 12px;
  text-align: center;
  text-transform: lowercase;
  color: #bbbbbb;
}

.Projects .link {
  font-style: normal;
  font-weight: normal;
  line-height: 8px;
  text-transform: lowercase;
  color: #c4c4c4;
}

.Projects .link > a {
  text-decoration: none;
  color: rgb(var(--color));
}
