input[type=color]{
	width: 12px;
	height: 12px;
	border: none;
	background: none;
}
input[type="color"]::-webkit-color-swatch-wrapper {
	padding: 0;
}
input[type="color"]::-webkit-color-swatch {
	border: solid 1px #E2E2E2; /*change color of the swatch border here*/
}

#color-picker{
    padding: 0;
}