.Modal {
  position: fixed;
  z-index: 9999;
  background: #fdfdfd;
  width: 70%;
  border: 2px solid #e2e2e2;
  box-shadow: 5px 5px 5px -5px #525252;
  padding: 8px 4px;
  left: 15%;
  top: 15%;
  box-sizing: border-box;
  transition: all 0.3s ease-out;
  letter-spacing: 1px;
}

@media (min-width: 600px) {
  .Modal {
    width: 500px;
    left: calc(50% - 250px);
  }
}
