.Background {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: rgb(var(--color), 1);
  background: radial-gradient(circle, rgba(var(--patternColor1),1) 0%, rgba(var(--patternColor2),1) 100%);
  -webkit-mask:  url(https://twpeng-portfolio.s3-us-west-1.amazonaws.com/vectors/Vector.svg) repeat 50% 50%;
  mask: url(https://twpeng-portfolio.s3-us-west-1.amazonaws.com/vectors/Vector.svg) repeat 50% 50%;
}
