.Skills {
  width: 470px;
  background-color: #fdfdfd;
  border: 1px solid #e2e2e2;
  box-sizing: border-box;
  margin: 25px;
  position: relative;
}

.Skills .wrapper {
  margin: 20px 20px 25px;
}

.Skills .sub-heading {
  font-size: 12px;
  padding: 13px 0 0px;
  color: #525252;
}

.Skills .skills {
  font-size: 10px;
  color: #000;
  padding: 2px;
  line-height: 12px;
}

.Skills .line {
  color: rgb(var(--color));
  font-size: 8px;
}
