.MusicWidget .music-player,
.music-info {
  margin: 15px 0;
  background-color: #eeeeee;
  width: 115%;
  border-radius: 4px;
  box-shadow: 5px 5px 5px -5px #525252;
}

.MusicWidget .music-player {
  padding: 20px 5px;
}

.MusicWidget .music-info {
  padding: 10px 9px 10px 5px;
}

.MusicWidget .track {
  padding: 0 10px;
  width: 100%;
}

.MusicWidget .volume {
  margin-top: 10px;
  padding: 0 30px;
  width: 100%;
}

.MusicWidget .music-info > * {
  padding: 0 4px;
}

.MusicWidget > div > div.dark-tag {
  color: #f8f8f8;
  background-color: #898989;
  border-radius: 3px;
  text-transform: uppercase;
  font-size: 9px;
  padding: 2px 5px;
  display: inline-block;
}

.MusicWidget .album-img {
  width: 35px;
  filter: grayscale(0.7);
  border-radius: 3px;
}

.MusicWidget .song-info {
  font-size: 10px;
}

.MusicWidget .track > img:hover {
  filter: brightness(0.6);
}

.MusicWidget .pause-btn {
  border: none;
  background-color: #898989;
  border-radius: 10px;
  width: 35px;
  height: 35px;
}

.MusicWidget .pause-btn:focus {
  outline: none;
}

.MusicWidget .pause-btn:hover {
  background-color: #525252;
}

#volumeInput {
  border-radius: 8px;
  height: 4px;
  width: 150px;
  outline: none;
  -webkit-appearance: none;
  background: linear-gradient(
    to right,
    #898989 0%,
    #898989 50%,
    #e2e2e2 50%,
    #e2e2e2 100%
  );
}

input[type="range"]::-webkit-slider-thumb {
  width: 1px;
  -webkit-appearance: none;
  height: 4px;
  border-radius: 2px;
}
