.Deco {
  background: #fdfdfd;
  border: 1px solid #e2e2e2;
  box-sizing: border-box;
  min-width: 210px;
  max-width: 210px;
}

.Deco button {
  border: 1px solid #e2e2e2;
  background-color: #fdfdfd;
  margin: 0 1px;
  padding: 0 4px;
  max-width: 20px;
  max-height: 17.5px;
}

.Deco button:hover {
  border: 1px solid #525252;
  background-color: #fdfdfd;
}

.Deco button:focus {
  border: 1px solid #e2e2e2;
  background-color: #fdfdfd;
  outline: none;
}

.Deco object{
  pointer-events: none;
}

.Deco .exe {
  position: absolute;
  left: 10px;
  font-size: 10px;
  line-height: 12px;
  top: 13px;
  color: #898989;;
}