.SidePanel {
  position: relative;
  background: #f8f8f8;
  border: 1px solid #e2e2e2;
  width: 355px;
  height: 100%;
  box-shadow: 5px 5px 5px -5px #525252;
}

.navbar {
  padding: 20px 34px 20px 36px;
}

.greeting {
  font-size: 11px;
  writing-mode: vertical-lr;
  transform: rotate(180deg);
  position: absolute;
  margin-top: 70px;
  right: 88%;
}

.rectangle-1 {
  background: #f5f5f5;
  border: 1px solid #e2e2e2;
  position: absolute;
  height: 80%;
  width: 90%;
  right: -1px;
}

.rectangle-2 {
  position: absolute;
  z-index: 1;
  float: right;
  right: -40px;
  margin-top: 28px;
}

.rectangle-3 {
  position: absolute;
  z-index: 3;
  right: 40px;
  top: 160px;
}

.rectangle-4 {
  background: #f8f8f8;
  border: 1px solid #e2e2e2;
  width: 225px;
  height: 185px;
  z-index: 1;
  position: absolute;
  right: 16px;
  top: 370px;
}

.circlelink {
  z-index: 1;
  position: absolute;
  top: 570px;
  right: 0;
  left: 0;
  display: flex;
  justify-content: center;
  margin-left: 37px;
}

.circlelink > * {
    margin: 0 11px;
}

.clock {
  position: absolute;
  float: right;
  right: -1px;
  bottom: 10px;
}

#color-input{
  position: absolute;
    float: right;
    right: 220px;
    bottom: 23px;
    padding: 0;
}
