.Buttons button {
    border: 1px solid #e2e2e2;
    background-color: #fdfdfd;
    margin: 0 1px;
    padding: 0 4px;
    max-width: 20px;
    max-height: 17.5px;
  }
  
  .Buttons button:hover {
    border: 1px solid #525252;
    background-color: #fdfdfd;
  }
  
  .Buttons button:focus {
    border: 1px solid #e2e2e2;
    background-color: #fdfdfd;
    outline: none;
  }