.AboutContainer {
}

.AboutContainer .left {
  height: 60%;
  min-height: 560px;
  width: 315px;
  position: relative;
  border: 1px solid #e2e2e2;
  background-color: #f8f8f8;
  right: -1px;
  box-shadow: 5px 5px 5px -5px #525252;
}

.AboutContainer .right {
  min-width: 540px;
  width: 37%;
  height: 67.5%;
  min-height: 690px;
  border: 1px solid #e2e2e2;
  background-color: #f8f8f8;
  position: relative;
  box-shadow: 5px 5px 5px -5px #525252;
}

.AboutContainer .subheading {
  font-size: 13px;
  line-height: 16px;
  color: #aaaaaa;
  margin-left: 20px;
  font-style: italic;
}

.AboutContainer .hashtags{
    text-transform: uppercase;
    font-size: 6.5px;
    width: 110%;
}

.AboutContainer .my-face{
    border-radius: 35px;
    width: 60px;
}

.AboutContainer .dark-tag{
    color: #f8f8f8;
    background-color: #898989;
    border-radius: 3px;
    text-transform: uppercase;
    font-size: 10px;
    padding: 5px;
    display: inline-block;
}

.AboutContainer .quote-wrapper{
    padding-left: 10px;
}

.AboutContainer .quote{
    margin: 10px 0;
    padding-left: 5px;
    border-left: 1px solid #e2e2e2;
    font-size: 11px;
}