.University {
  width: 470px;
  background-color: #fdfdfd;
  border: 1px solid #e2e2e2;
  box-sizing: border-box;
  margin: 25px 25px 0;
  position: relative;
}

.University .wrapper {
  margin: 20px 20px 25px;
}

.University .sub-heading {
  font-size: 12px;
  padding: 13px 0 0px;
  color: #525252;
}

.University .title {
  color: #898989;
  font-size: 12px;
  padding: 4px 0 8px;
}

.University .school-courses {
  padding: 12px 0 5px;
}

.University .courses {
  font-size: 10px;
  color: #000;
  padding: 2px;
  line-height: 12px;
}